.wrapperprofile{
    position: absolute;
    width:1100px;
    left:50%;
    transform:translateX(-50%);
    margin-top: 70px;
}
.wrappercontentProfile{
    display: flex;
    position:relative;
    justify-content:center;
    align-items: center;
}
.containerprofileContent{
    display:flex;
    flex-direction:column;
    row-gap:26px;
    padding: 40px;
}
.wrapperprofileFirst,
.wrapperprofileSecond,
.wrapperprofileThird{
    display:flex;
    align-items: center;
    margin-right: 200px;
}
.wrapperprofileFirst>.name{
    font-weight: 200;
}
.wrapperprofileFirst>.editar{
    font-size: 14.49px;
    font-weight: 600;
    margin: 0 10px 4px 10px;
    padding: 5px 9px;
    border-radius: 3px;
    border: 1px solid #2626264f
}
.wrapperprofileSecond{
    width: 317px;
    justify-content: space-between;
}
.wrapperprofileThird ul{
    list-style-type:none;
}

.wrapperprofileImg{
    width: 150px;
    height: 150px;
    position:absolute;
    margin-right: 100px;
    top: 10%;
    display: flex;
    position: static;
}

.wrapperprofileImg img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
    display: flex;
    position: static;
}

.configuracao{
    width: 24px;
    height: 24px;
    margin: 0 0 3px 5px;
    cursor: pointer;
}

.configuracao img{
    width: 100%;
    height: 100%;
}

.destaque {
    justify-content: center;
    align-items: center;
    margin-left: -180px;
    display: flex;
}
.foto img{
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid rgba(105, 105, 105, 0.447);
    margin: 10px 23px;
    padding: 2px;
    cursor: pointer;
    display: flex;
}
.linha-1 {
    width: 964px;
    border: 1px solid rgba(161, 158, 158, 0.251);
    margin: 40px 100px 0px;
}
.linha-2 {
    width: 110px;
    border: 1px solid rgba(11, 11, 11, 0.474);
    margin: -2px 350px 0px
}
.publi-1 {
    display: flex;
    margin-left: 85px;
}
.publi-1 img {
    margin: 40px;
    margin-right: -15px;
}
