.header {
    background: var(--background);
    border-bottom: 1px solid var(--border);
    position: fixed;
    z-index: 9999;
    top: 0;
    width: 100%;
}
.header .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 60px;
    max-width: 957px;
    margin: 0 auto;
    width: 90%;
}
.header .container .logo {
    width: 111px;
    cursor: pointer;
}
.input-fake {
    background: var(--background-main);
    border-radius: 3px;
    display: flex;
    align-items: center;
    padding: 3px 10px;
}

.input-fake input {
    font-size: 13px;
    padding: 0 11px;
}
.icones_header {
    gap: 16px;
    display: flex;
    cursor: pointer;
    align-items: center;
}
.icones_header .img-user{
    width: 25px;
    height: 25px;
    border-radius: 100%;
}
