.header-post {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 15px;
}
.header-post .img-header-post {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.img-header-post {
    object-fit: cover;
}
.infos-post {
    display: flex;
    align-items: center;
    gap: 16px;
}
.img-post {
    width: 100%;
    object-fit: cover;
    position: static;
}
.img-post-edit {
    width: 100%;
    
}
.footer-post {
    padding: 10px 15px;
}
.icones-post {
    display: flex;
    justify-content: space-between;
}
.icones-post .icons-one {
    display: flex;
    gap: 16px;
    cursor: pointer;
}
.icon {
    cursor: pointer;
}
.like {
    margin-top: 6px;
}
.like span {
    font-size: 15px;
    font-weight: 500;
}
.legendas {
    margin: 6px 0 3px;
}
.legendas p span {
    font-weight: 500;
}
.time-post time {
    font-size: 11px;
    color: var(--secondary);
}
.config {
    cursor: pointer;
}
.comentario {
    border-top: 1px solid var(--border-secondary);
    margin-top: 8px;
    display: flex;
}
.comentario-fake {
    padding: 15px 0;
    display: flex;
    align-items: center;
    flex: 1;
    gap: 16px;
}
.comentario-fake input {
    font-size: 16px;
}
.comentario button {
    cursor: pointer;
    color: var(--quartiary);
    font-weight: 500;
}
