.container {
    padding: 15px;
    display: flex;
    gap: 16px;
    text-align: center;
}
.story-elements {
    cursor: pointer;
}
.user-elements div {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(#c82d8e, #f99d4c);
    text-align: right;
    display: flex;

}
.image-user-story {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    border: 2px solid var(--background);
    cursor: pointer;
    object-fit: cover;
}
.user-elements span {
    font-size: 12px;
    cursor: pointer;
    
    
}